.resume_frame {
  background-color: #fafafa;
  border-radius: 20px;
  border: none;
  opacity: 0;
  animation: resumeAnim 0.5s ease 0s 1 normal forwards;
}

.wrapper {
  display: flex;
  width: 100%;
  justify-content: center;
  align-items: center;
}

@keyframes resumeAnim {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}
